const script = document.createElement('script');
let ok = false;
window.addEventListener('scroll', () => {
  if (ok === false) {
    ok = true;
    setTimeout(() => {
      script.src = 'https://api-maps.yandex.ru/2.1/?apikey=726b8561-cba3-487c-a8b4-e3e92f1591d0&lang=ru_RU';
      document.body.appendChild(script);
    }, 1000);
  }
});

